            </li>
            <li>
              <button class="them-change" @click="changTheme">
                <i
                  :class="
                    currentTheme === 'light'
                      ? 'fa-regular fa-sun'
                      : 'fa-solid fa-moon'
                  "
                ></i>
              </button>
