<template>
  <div
    :id="props.obj.id"
    :class="{
      big: props.size === 'big',
      Med: props.size === 'Med',
      small: props.size === 'small',
    }"
  >
    <div class="img position-relative">
      <div
        @click="addToFav"
        ref="action"
        v-if="props.size === 'Med' || props.size === 'small'"
        class="position-absolute action"
      >
        <i class="fa-solid fa-heart"></i>
      </div>
      <RouterLink
        v-if="props.size !== 'big'"
        :to="{ name: 'recipeDetails', params: { id: props.obj.id } }"
        ><img
          aria-label="know more about this recipe "
          width="100%"
          loading="lazy"
          height="100%"
          :src="props.obj.image"
          alt=""
      /></RouterLink>
      <img v-else width="100%" height="100%" :src="props.obj.image" alt="" />
    </div>
    <div class="info">
      <div>
        <h3
          style="font-weight: 900"
          :class="{ big_tital: props.size === 'big' }"
          class=""
        >
          {{
            props.obj.title.length > 29
              ? `${props.obj.title.slice(0, 28)}..`
              : props.obj.title
          }}
        </h3>
        <p class="summry" v-if="props.size === 'big'">
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quaerat
          delectus in ipsum error libero. Asperiores quibusdam ea laboriosam
          quod porro fugiat.
        </p>
      </div>

      <ul class="tags p-0 m-0">
        <li>
          <i class="fa-solid fa-utensils me-2"></i>
          {{ props.obj.dishTypes[0] || "Food" }}
        </li>
        <li>
          <i class="fa-solid fa-clock me-2"></i>
          {{ props.obj.readyInMinutes }} Minutes
        </li>
      </ul>

      <div v-if="props.size === 'big'" class="crdit">
        <RouterLink
          :to="{ name: 'recipeDetails', params: { id: props.obj.id } }"
          ><TheBotton
            :type="'primary'"
            class="p-3 d-flex ms-auto align-items-center justify-content-center"
          >
            <template #Text>
              <p class="m-0 me-1">View Recipes</p>
              <i class="fa-solid fa-play"></i>
            </template> </TheBotton
        ></RouterLink>
      </div>
    </div>
  </div>
</template>
<script setup>
import TheBotton from "@/components/TheBotton.vue";
import { defineProps, onMounted, ref } from "vue";
//icons
//<i class="fa-solid fa-utensils"></i>
//<i class="fa-regular fa-heart"></i>
//<i class="fa-solid fa-clock"></i>
const action = ref(null);
let cont = ref(0);

const addToFav = () => {
  if (cont.value <= 0) {
    cont.value += 1;
    action.value.firstChild.style.cssText = `color:red;`;
  } else {
    action.value.firstChild.style.cssText = `color:#ddd;`;
    cont.value = 0;
  }
};

const props = defineProps({ obj: Object, size: String });
onMounted(() => {});
</script>

<style lang="scss" scoped>
.big,
.Med,
.small {
  display: flex;
  border-radius: 1.88rem;
  .info {
    text-align: left;
    div {
      h3,
      p.summry {
        color: var(--text-color);
      }
    }
    .tags {
      display: flex;
      li {
        display: flex;
        align-items: center;
        color: var(--text-color);
        border-radius: 25px;
        padding: 0px 20px;
        min-width: 7.44rem;
        min-height: 2.5rem;
        margin: 0px 5px;
        i {
          color: var(--text-color);
          margin-right: 2px;
        }
      }
    }
  }
}

.big {
  background-color: var(--main-background);
  overflow: hidden;
  display: flex;
  max-width: 80rem;
  height: 40rem;
  flex-direction: row-reverse;
  justify-content: center;
  div.img {
    flex-basis: 50%;
  }
  div.info {
    flex-basis: 50%;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    div {
      h3.big_tital {
        font-size: 4rem;
      }
      .summry {
        margin-top: 55px;
      }
    }
    ul {
      li {
        background-color: rgba(0, 0, 0, 0.05);
        justify-content: center;
      }
    }
  }
}

.Med,
.small {
  background-image: linear-gradient(
    to top,
    var(--main-background),
    var(--body-background)
  );
  padding: 1rem;
  flex-direction: column;
  justify-content: space-evenly;
  div.img {
    margin-bottom: 15px;
    img {
      border-radius: 1.88rem;
    }
    div.action {
      font-size: 22px;
      width: 48px;
      height: 48px;
      background-color: white;
      color: #ddd;
      display: flex;
      justify-content: center;
      align-items: center;
      right: 15px;
      top: 14px;
      border-radius: 50%;
    }
  }
}

.Med {
  max-width: 25rem;
  height: 27.13rem;
  div.img {
    img {
      border-radius: 1.88rem;
    }
  }
}
.small {
  max-width: 18.13rem;
  max-height: 19.75rem;
  div {
    h3 {
      font-size: 1.13rem;
    }
  }
  .info {
    .tags {
      li {
        padding: 0px 10px;
      }
    }
  }
}
@media (max-width: 767px) {
  .big {
    div.img {
      display: none;
    }
    .info {
      align-items: center;
      div {
        .big_tital {
          font-size: 3rem !important;
        }
        .big_tital,
        .summry {
          text-align: center;
        }
      }
      ul {
        margin: 0.7rem 0 !important;
      }
    }
  }
}
</style>
