<template>
  <div class="preant">
    <h2
      ref="titleH2"
      v-if="props.title"
      :class="
        props.type === 'main-tital'
          ? 'main-tital'
          : props.type === 'sub-tital'
          ? 'sub-tital'
          : 'small-tital'
      "
    ></h2>
    <p ref="subtitleP" v-if="props.subtitle" class="sub-main-tital"></p>
  </div>
</template>
<script setup>
import { defineProps, onMounted, ref } from "vue";
const titleH2 = ref(null);
const subtitleP = ref(null);
const props = defineProps({
  title: Boolean,
  subtitle: Boolean,
  type: String,
  titalText: String,
  subtitleText: String,
});
onMounted(() => {
  titleH2.value.innerHTML = props.titalText;
  if (props.subtitle) {
    subtitleP.value.innerHTML = props.subtitleText;
  }
});
</script>
<style lang="scss" scoped>
div.preant {
  h2 {
    color: var(--text-color);
    font-weight: bold;
  }
  h2.main-tital {
    font-size: 4rem;
    font-weight: bold;
  }
  p.sub-main-tital {
    color: var(--sub-text);
    font-size: 1rem;
  }
  h2.sub-tital {
    font-size: 3rem;
  }
  h2.small-tital {
    font-size: 2.25rem;
    font-weight: inherit;
  }
}
@media (max-width: 320px) {
  div.preant {
    h2.sub-tital {
      font-size: 2.5rem;
    }
  }
}
</style>
