import { createRouter, createWebHistory } from "vue-router";
import HomeVue from "@/views/HomeVue.vue";
const routes = [
  {
    path: "/",
    name: "HomeVue",
    component: HomeVue,
  },
  {
    path: "/recipeDetails/:id",
    name: "recipeDetails",
    component: () => import("@/views/RecipeDetails.vue"),
  },
  {
    path: "/BlogsAndArticles",
    name: "BlogsAndArticles",
    component: () => import("@/views/BlogArticle.vue"),
  },
  {
    path: "/BlogsAndArticles/ArticleVue/:id",
    name: "ArticleVue",
    component: () => import("@/views/ArticleVue.vue"),
  },
  {
    path: "/ContactUs",
    name: "ContactUs",
    component: () => import("@/views/ContactUs.vue"),
  },
  {
    path: "/AboutUs",
    name: "AboutUs",
    component: () => import("@/views/AboutUS.vue"),
  },
  {
    path: "/:pathMatch(.*)*",
    name: "NotFound",
    component: () => import("@/views/4O4.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
